@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&display=swap");
.hero img {
  width: 100%;
  height: auto;
}
.hero h1 {
  width: 70%;
}
.hero p {
  color: hsl(236deg, 13%, 42%);
  font-size: 0.8rem;
  letter-spacing: 1px;
  line-height: 1.7em;
  font-weight: 400;
}
.hero button {
  border: none;
  color: hsl(36deg, 100%, 99%);
  background-color: hsl(5deg, 85%, 63%);
  font-size: 0.7rem;
  padding: 1em 2.5em;
  letter-spacing: 1.5px;
}
.hero button:hover {
  cursor: pointer;
  background-color: hsl(240deg, 100%, 5%);
}

@media only screen and (min-width: 1439px) {
  .hero {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .hero img {
    width: 100%;
    height: 100%;
  }
  .hero .hero-content {
    display: flex;
    justify-content: flex-start;
    gap: 4em;
    padding: 1.5em 0 0 0;
  }
  .hero .hero-content h1 {
    padding: 0;
    margin: 0;
    font-size: 3rem;
  }
  .hero .hero-content .hero-items {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    width: 85%;
  }
  .hero .hero-content .hero-items p {
    padding: 0;
    margin: 0 0 1em 0;
    font-size: 0.9rem;
    letter-spacing: 0;
  }
  .hero .hero-content .hero-items button {
    font-size: 0.9rem;
  }
}/*# sourceMappingURL=Hero.css.map */